import React from 'react'
import {withRouter,NavLink} from 'react-router-dom'
import './Projects/portfolio/css/nails.css'
import videonails from './Projects/portfolio/videos/nails2.mp4'
import nailsVideo from './Projects/portfolio/videos/nails-video.mp4'
import image1 from "./Projects/portfolio/images/nails/nails-joys.jpg"
import image2 from './Projects/portfolio/images/nails/nails-novi.jpg'
import image3 from './Projects/portfolio/images/nails/IMG_20210219_103240_326.jpg'
import image4 from './Projects/portfolio/images/nails/nails-joy.jpg'
import image5 from './Projects/portfolio/images/nails/IMG_20210219_103219_112.jpg'
import { FaFacebookSquare,FaLinkedin,FaInstagram,
  FaBars,FaTimesCircle
 } from "react-icons/fa"

const blog = () =>
{

  const  onClickHandler = () => {
    const navbar = document.querySelector("#nav");
    const navBtn = document.querySelector("#nav-btn");
    const closeBtn = document.querySelector("#close-btn");
    const sidebar = document.querySelector("#sidebar");
   
// add fixed class to navbar
window.addEventListener("scroll", function () {
  if (window.pageYOffset > 80) {
    navbar.classList.add("navbar-fixed");
    
  } else {
    navbar.classList.remove("navbar-fixed");
  }
});
// show sidebar
navBtn.addEventListener("click", function () {
sidebar.classList.add("show-sidebar");
});
closeBtn.addEventListener("click", function () {
sidebar.classList.remove("show-sidebar");
});
// set year
  }
return (
 
    <div> 

    <nav className="nav-nails" id="nav">
      <div className="nav-center-nails">
       
        <div className="nav-header-nails">
            <h2 className="nails">A {'{ }'} B</h2>
          <button onClick={onClickHandler} className='nav-btn' id='nav-btn'>
            <FaBars />
          </button>
          
        </div>
      
        <ul className="nav-links-nails">
          <li>
           
            <NavLink to="/">Home</NavLink>
          </li>
         
          <li>
            <a href="#project">Projects</a>
          </li>
         
          <li>
            <a href="/Contact">Contact</a>
          </li>
        </ul>
      </div>
    </nav>
    
    <aside className="sidebar" id="sidebar">
      <div>
      <button className='close-btn' id='close-btn'>
          <FaTimesCircle />
        </button>
        
        <ul className="sidebar-links">
          <li>
            <NavLink to="/">Home</NavLink>
          </li>
         <li>
            <NavLink to="/project1">Project-Nails-1</NavLink>
          </li>
          <li>
            <NavLink to="/project2">Project-Nails-2</NavLink>
          </li>
          <li>
            <NavLink to="/Station">Project-Station</NavLink>
          </li>
          <li>
            <NavLink to="/Contact">Contacts</NavLink>
          </li>
        </ul>
       
        <ul className="social-icons-nails">
          <li>
          <a target="_blank" rel="noreferrer" href="https://www.facebook.com/aleksandar.a.boskovski"
            className='social-icon'>
          <FaFacebookSquare />
          </a>
          </li>
          <li>
            <a target="_blank" rel="noreferrer" href="https://www.linkedin.com/in/aleksandar-boskovski-85ba1a160/"
            className='social-icon'>
          <FaLinkedin />
          </a>
          </li>
          <li>
          <a target="_blank" rel="noreferrer" href="https://www.instagram.com/aleksandar.a.boskovski/"
            className='social-icon'>
          <FaInstagram />
          </a>
          </li>
        </ul>
      </div>
    </aside>
   
    <header className="hero-nails">
      <video 
       controls
        autoPlay
        muted
        loop
        className="video-container-nails"
        poster='./images/natalie-nails-c.jpg'
      >   
        <source src={videonails}
        type="video/mp4"
        /> 
       <h2>Your browser not supported embeded video</h2>
      </video>
      <div className="section-center-nails hero-center-nails">
        <article className="hero-info-nails">
          <div className="underline"></div>
          <h1 className='nails'>Profesional art work for nails!</h1>
          
          <NavLink to="/Contact" className="btn-nails hero-btn-nails">Let's Talk!</NavLink>
         
          <ul className="social-icons-nails hero-icons-nails">
            <li>
              <a href="https://www.facebook.com/aleksandar.a.boskovski" className="social-icon-nails">
              <FaFacebookSquare />
              </a>
            </li>
            <li>
            <a target="_blank" rel="noreferrer" href="https://www.linkedin.com/in/aleksandar-boskovski-85ba1a160/"
            className='social-icon-nails'>
          <FaLinkedin />
          </a>
          </li>
            <li>
              <a href="https://www.instagram.com/aleksandar.a.boskovski" className="social-icon-nails">
              <FaInstagram />
              </a>
            </li>
          </ul>
      </article>  
      </div>
    </header>
    <section className="section-nails about-nails">
      <div className="section-center-nails about-center-nails">
        
        <article className="about-img-nails">
          <img
            src={image1}
            className="hero-photo-nails"
            alt="about img"
          />
        </article>
      
        <article className="about-info-nails">
      
          <div className="section-title-nails about-title-nails">
            <h2 className='colorfont'>Nails~Room</h2>
            <div className="underline"></div>
          </div>
          
          <h4 className ='nails'>Profesional art work for your needs!
          </h4>        
          <a href="/" className="btn-nails">More...</a>
        </article>
      </div>
    </section>

    <section id="project" className="section-nails  projects-nails">
     
      <div className="section-title-nails">
        <h2 className='nails'>My Projects</h2>
        <div className="underline"></div>
     
            <h3 className="projects-text">
         I hope you like it
        </h3>                     
      </div>
      <div className="section-center-nails projects-center-nails">
       
        <a href="/project1" rel="noreferrer" className="project-1-nails">
          <article className="project-nails">
            <img
              src={image2}
              alt="single project"
              className="project-img-nails"
            />
            <div className="project-info-nails">
              <h4 className="nails">Nails make up!</h4>
              <p></p>
            </div>
          </article>
        </a>
      
        <a href="/project2" rel="noreferrer" className="project-2-nails">
          <article className="project-nails">
            <img
              src={image3}
              alt="single project"
              className="project-img-nails"
            />
            <div className="project-info-nails">
              <h4 className="nails">Nails gel</h4>
              <p></p>
            </div>
          </article>
        </a>
     
        <a href="#" className="project-3-nails">
          <article className="project-nails">
            <img
              src={image4}
              alt="single project"
              className="project-img-nails"
            />
            <div className="project-info-nails">
              <h4 className="nails">Cirkons</h4>
              <p></p>
            </div>
          </article>
        </a>
        
        <a href="#" className="project-4-nails">
          <article className="project-nails">
            <img
              src={image5}
              alt="single project"
              className="project-img-nails"
            />
            <div className="project-info-nails">
              <h4 className="nails">Colorfull</h4>
              <p></p>
            </div>
          </article>
        </a>
       
      </div>
    </section>
 
    <section className="connect-nails">
      <video
        controls
        autoPlay
        muted
        loop
        className="video-container-nails"
        poster="./images/project-1.jpeg"
      >
        <source src={nailsVideo} 
        type="video/mp4" />
        Sorry, your browser does not support embedded videos
      </video>
      <div className="video-banner-nails">
        
        <div className="section-title-nails">
          <h2 className="nails">Your idea our realization</h2>
          <div className="underline"></div>
        </div>
        <NavLink to="/Contact" className="btn-nails">Let's Talk!</NavLink>
      </div>
    </section>
  
    <footer className="footer">
     
      <ul className="social-icons">
      <li>
      <a target="_blank" rel="noreferrer" href="https://www.facebook.com/aleksandar.a.boskovski"
      className='social-icon-nails'>
         <FaFacebookSquare />
    </a>
    </li>
        <li>
      <a target="_blank" rel="noreferrer" href="https://www.linkedin.com/in/aleksandar-boskovski-85ba1a160/"
      className='social-icon-nails'>
    <FaLinkedin />   
   </a>
    </li>
        <li>
          <a href="https://www.instagram.com/aleksandar.a.boskovski/" rel="noreferrer" target="_blank" className="social-icon-nails">
          <FaInstagram />
          </a>
        </li>
      </ul>

      <p className='colorfont'>&copy; <span id="date">2021</span> A {'{}'} B all rights reserved</p>
    </footer>
    <script src="./js/app.js"></script>
    </div>
)
} 
export default withRouter(blog)