import React from 'react'
import './Project-TeaStation/css/station.css'
import imgStation from './Project-TeaStation/images/about-bcg.jpeg'
import imgStation1 from './Project-TeaStation/images/product-1.jpeg'
import imgStation2 from './Project-TeaStation/images/product-2.jpeg'
import imgStation3 from './Project-TeaStation/images/product-3.jpeg'
import {NavLink} from 'react-router-dom'
import imgProject from './Project-TeaStation/images/project-1.jpeg'
import imgProject1 from './Project-TeaStation/images/project-2.jpeg'
import imgProject2 from './Project-TeaStation/images/project-3.jpeg'
import imgProject3 from './Project-TeaStation/images/project-4.jpeg'

import { FaFacebookSquare,FaLinkedin,FaInstagram,
  FaBars,FaTimesCircle
 } from "react-icons/fa"
const Station = () => {
  const  onClickHandler = () => {
    const navbar = document.querySelector("#nav");
    const navBtn = document.querySelector("#nav-btn");
    const closeBtn = document.querySelector("#close-btn");
    const sidebar = document.querySelector("#sidebar");
    const navlinksection = document.querySelector("#nav-link-station")
    

// add fixed class to navbar
    window.addEventListener("scroll", function () {
      if (window.pageYOffset > 60) {
        navbar.classList.add("navbar-fixed");
        navlinksection.classList.add("nav-not");
      } else {
        navbar.classList.remove("navbar-fixed");
      }
    });
// show sidebar
navBtn.addEventListener("click", function () {
sidebar.classList.add("show-sidebar");
});
closeBtn.addEventListener("click", function () {
sidebar.classList.remove("show-sidebar");
});
// set year
  }

return (
    <div>
      <nav className="nav-station" id="nav">
      <div className="nav-center-station">
        
        <div className="nav-header-station">
          <h2>A {'{}'} B</h2>
          <button onClick={onClickHandler} className='nav-btn' id='nav-btn'>
            <FaBars />
          </button>
        </div>
       
        <ul className="nav-links-station" id="nav-link-station">
          <li>
            
            <NavLink to='/'>Home</NavLink>
          </li>
          
          <li>
            <NavLink to="/">projects</NavLink>
          </li>
          <li>
            <NavLink to="/Contact">contact</NavLink>
          </li>
        </ul>
      </div>
    </nav>
  
    <aside className="sidebar" id="sidebar">
      <div>
      <button className='close-btn' id='close-btn'>
          <FaTimesCircle />
        </button>
       
        <ul className="sidebar-links">
          <li>
            <NavLink to="/">home</NavLink>
          </li>
         
          <li>
            <NavLink to="/Nails">project-Nails</NavLink>
          </li>
          <li>
            <NavLink to="/Contact">contact</NavLink>
          </li>
        </ul>
        
        <ul className='social-icons'>
          <li>
            <a target="_blank" rel="noreferrer" href="https://www.facebook.com/aleksandar.a.boskovski"
            className='social-icon'>
          <FaFacebookSquare />
          </a>
          </li>
          <li>
            <a target="_blank" rel="noreferrer" href="https://www.linkedin.com/in/aleksandar-boskovski-85ba1a160/"
            className='social-icon'>
          <FaLinkedin />
          </a>
          </li>
          
          <li>
            <a target="_blank" rel="noreferrer" href="https://www.instagram.com/aleksandar.a.boskovski/"
            className='social-icon'>
          <FaInstagram />
          </a>
          </li>
        </ul>
      </div>
    </aside>
   
         <header className='header-station'>
      <div className='banner-station'>
        <h2>Over one hundrend Flawor</h2>
        <h1>Specially <br /> Crafted Tea</h1>
        <NavLink to="/Contact" className='btn-station banner-btn-station'>Let's Talk!</NavLink>
      </div>

    </header>
    <div className='content-divider-station'>
    </div>
    <section className='skills-station clearfix'>
     
      <article className='skill-station'>
        <span className='skill-icon-station'>
          <i className='fas fa-mug-hot'></i>
        </span>
        <h4 className="skill-title">made in Europe</h4>
        <p className='skill-text-station'>
          Lorem, ipsum dolor sit amet consectetur adipisicing elit. 
          Ipsum expedita cum amet provident error.
           Quia soluta.
        </p>
      </article>
    
      <article className='skill-station'>
        <span className='skill-icon-station'>
          <i className='fas fa-glass-cheers'></i>
        </span>
        <h4 className="skill-title">relaxation</h4>
        <p className='skill-text-station'>
          Lorem, ipsum dolor sit amet consectetur adipisicing elit. 
          Ipsum expedita cum amet provident error.
           Quia soluta.
        </p>
      </article>
      
      <article className='skill-station'>
        <span className='skill-icon-station'>
          <i className='fas fa-bomb'></i>
        </span>
        <h4 className="skill-title">energy</h4>
        <p className='skill-text-station'>
          Lorem, ipsum dolor sit amet consectetur adipisicing elit. 
          Ipsum expedita cum amet provident error.
           Quia soluta.
        </p>
      </article>
    
      <article className='skill-station'>
        
        <span className='skill-icon-station'>
          <i className='fas fa-mortar-pestle'></i>
        </span>
        <h4 className="skill-title">Learning</h4>
        <p className='skill-text-station'>
          Lorem, ipsum dolor sit amet consectetur adipisicing elit. 
          Ipsum expedita cum amet provident error.
           Quia soluta.
        </p>
      </article>
      
    </section>
   
    
    <section>
      <div className='section-center-station clearfix'>
        <article className='about-img-station'>
          <div className='about-picture-container-station'>
            <img src={imgStation} 
            className='about-picture-station' alt='Tea Kettle' />
          </div>
        </article>
        <article className='about-info-station'>
          <div className='section-title-station'>
            <h3>about our</h3>
            <h2>Hello</h2>
          </div>
          <p className='about-text-station'>
            Programing for Life with good Tea.
          </p>
          <a href='#' className='btn-station spec-btn-station'>learn more</a>
        </article>
      </div>
    </section>
  
    <section className="products-station">
      <div className='section-center-station clearfix'>
        <article className='products-info-station'>
          <div className='section-title-station'>
            <h3>check out</h3>
            <h2>products</h2>          
          </div>
          <p className='products-text-station'>
            What you can dream today.Can quickly become your reality tomorrow
          </p>
         <a href='#' className='btn-station spec-btn-station'>inventory</a>
        </article>
        <article className='products-inventory-station clearfix'>
         <div className='product-station'>
          <img src={imgStation1} alt='about tea'
           className='product-img-station'/>
           <h4 className='product-title-station'>ginger tea</h4>
           <h4 className='product-price-station'>$ 9.65</h4>
         </div>
         <div className='product-station'>
          <img src={imgStation2} alt='about tea'
           className='product-img-station'/>
           <h4 className='product-title'>white tea</h4>
           <h4 className='product-price-station'>$ 9.65</h4>
         </div>
         <div className='product-station'>
          <img src={imgStation3} alt='about tea'
           className='product-img-station'/>
           <h4 className='product-title'>black tea</h4>
           <h4 className='product-price-station'>$ 9.65</h4>
         </div>
        </article>
      </div>
    </section>
    <section className="section-blog timeline">
     
      <div className="section-title-timeline">
        <h2>timeline</h2>
        <div className="underline"></div>
      </div>
     
      <div className="section-center-timeline timeline-center">
        
        <article className="timeline-item">
          <h4>2020</h4>
          <p>
            Online Events
          </p>
          <span className="number">
            1
          </span>
        </article>
        
        <article className="timeline-item">
          <h4>2019</h4>
          <p>
            Angular
          </p>
          <span className="number">
            2
          </span>
        </article>
        
        <article className="timeline-item">
          <h4>2018</h4>
          <p>
            Vue.js
          </p>
          <span className="number">
            3
          </span>
        </article>
        
        <article className="timeline-item">
          <h4>2017</h4>
          <p>
           React.js
          </p>
          <span className="number">
            4
          </span>
        </article>
        
        <article className="timeline-item">
          <h4>2016</h4>
          <p>
            Node js
          </p>
          <span className="number">
            5
          </span>
        </article>
      
        <article className="timeline-item">
          <h4>2015</h4>
          <p>
           Java script
          </p>
          <span className="number">
            6
          </span>
        </article>
        
      </div>
    </section>
    
    <section className="section-blog blog">
      
      <div className="section-title">
        <h2>blog</h2>
        <div className="underline"></div>
      </div>
      
      <div className="section-center-blog blog-center">
      
        <div className="card">
         
          <div className="card-side card-front">
            <img src={imgProject} alt="" />
            <div className="card-info">
              <h4>article title</h4>
              <p>
                Learning fundamentals of Java Script
              </p>
              <div className="card-footer">
                <img src={imgProject} alt="author" />
                <p>3 min read</p>
              </div>
            </div>
          </div>
          
          <div className="card-side card-back">
            <button className="btn-blog">read more</button>
          </div>
        </div>
       
        <div className="card">
          
          <div className="card-side card-front">
            <img src={imgProject1} alt="" />
            <div className="card-info">
              <h4>article title</h4>
              <p>
                Learning React.js
              </p>
              <div className="card-footer">
                <img src={imgProject1} alt="author" />
                <p>5 min read</p>
              </div>
            </div>
          </div>
         
          <div className="card-side card-back">
            <button className="btn-blog">read more</button>
          </div>
        </div>
        
        <div className="card">
         
          <div className="card-side card-front">
            <img src={imgProject2} alt="" />
            <div className="card-info">
              <h4>article title</h4>
              <p>
               Learning UI/UX Design
              </p>
              <div className="card-footer">
                <img src={imgProject2} alt="author" />
                <p>4 min read</p>
              </div>
            </div>
          </div>
          
          <div className="card-side card-back">
            <button className="btn-blog">read more</button>
          </div>
        </div>
        
        <div className="card">
        
          <div className="card-side card-front">
            <img src={imgProject3} alt="" />
            <div className="card-info">
              <h4>article title</h4>
              <p>
               Learnig Angular.
              </p>
              <div className="card-footer">
                <img src={imgProject3} alt="author" />
                <p>1 min read</p>
              </div>
            </div>
          </div>
          
          <div className="card-side card-back">
            <button className="btn-blog">read more</button>
          </div>
        </div>
       
      </div>
    </section>
    
    
    </div>
)
}
export default Station