import React from 'react'
import './Contact.css'
import {NavLink} from 'react-router-dom'
import videoContact from './Projects/portfolio/videos/connect.mp4'
import { FaFacebookSquare,FaLinkedin,FaInstagram,
  FaBars,FaTimesCircle
 } from "react-icons/fa"
const Contact = () => 
{

  const onClickHandler = () => {
    const navbar = document.querySelector("#nav");
    const navBtn = document.querySelector("#nav-btn");
    const closeBtn = document.querySelector("#close-btn");
    const sidebar = document.querySelector("#sidebar");
    
// add fixed class to navbar
    window.addEventListener("scroll", function () {
      if (window.pageYOffset > 80) {
        navbar.classList.add("navbar-fixed");
      } else {
        navbar.classList.remove("navbar-fixed");
      }
    });
// show sidebar
navBtn.addEventListener("click", function () {
sidebar.classList.add("show-sidebar");
});
closeBtn.addEventListener("click", function () {
sidebar.classList.remove("show-sidebar");
});
// set year

}
   return (
    <div>
      <div className='nav' id='nav'>
      <div className='nav-center'>
        <div className='nav-header'>
        <h2 className='logo nav-logo'>A {"{}"} B</h2>
        
          <button onClick={onClickHandler} className='nav-btn' id='nav-btn'>
            <FaBars />
          </button>
        </div>
        <ul className="nav-links-contact">
          <li>
            <NavLink exact activeClassName='active-class' to='/'>Home</NavLink>
          </li>
 
        </ul>
      </div>
    </div>
    
    <aside className='sidebar' id='sidebar'>
      <div>
        <button className='close-btn' id='close-btn'>
          <FaTimesCircle />
        </button>
        <ul className="sidebar-links">
          <li>
            <a href='/'>Home</a>
          </li>    
        </ul>
        <ul className='social-icons'>
          <li>
            <a target="_blank" rel="noreferrer" href="https://www.facebook.com/aleksandar.a.boskovski"
            className='social-icon'>
          <FaFacebookSquare />
          </a>
          </li>
          <li>
            <a target="_blank" rel="noreferrer" href="https://www.linkedin.com/in/aleksandar-boskovski-85ba1a160"
            className='social-icon'>
          <FaLinkedin />
          </a>
          </li>
          
          <li>
            <a target="_blank" rel="noreferrer" href="https://www.instagram.com/aleksandar.a.boskovski"
            className='social-icon'>
          <FaInstagram />
          </a>
          </li>
        </ul>
      </div>
    </aside>
 <section className="connect-contact">
      <video
        controls
        autoPlay
        muted
        loop
        className="video-contact"
        poster="./images/project-1.jpeg"
      >
        <source src={videoContact} type="video/mp4" />
        Sorry, your browser does not support embedded videos
      </video>
      <div className="video-banner-contact">
        
        <div className="section-title-contact">
          <h2>let's get in touch</h2>
          <div className="underline"></div>
        </div>
       
        <h4 className="video-text-contact">
         You Can Contact me via E-mail:
         
        aleksandarboskovski24@gmail.com
        <br /><br />
         You Can Contact me vie Linked-In: <a href="https://www.linkedin.com/in/aleksandar-boskovski-85ba1a160" rel="noreferrer" target="_blank">Aleksandar Boskovski</a>
        </h4>
        
      </div>
    </section>
    </div>
)
}
export default Contact